/* animations */
@keyframes pop {
  0%{
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

.animate{
  animation: pop 1.5s;
}

.fastAnimate{
  animation: pop .05s;
}

/* Error page */
.errorPage-wrapper{
